/* Global styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Color scheme */
  :root {
    --primary-color: #0077B6;
    --secondary-color: #CCCCCC;
    --text-color: #333333;
    --background-color: #FFFFFF;
  }